<template>
  <div @click="toggle" class="d-flex justify-content-between w-100">
    <div class="mr-2">
      <span class="title">{{ title }}</span>
      <br />
      <div class="desc">{{ desc }}</div>
    </div>
    <toggle-button
      class="mt-1"
      color="#377EF9"
      :value="value"
      sync
      disabled
      :width="42"
      :height="25"
      :margin="2"
    />
  </div>
</template>
<script>
export default {
  props: {
    title: String,
    desc: String,
    value:{
      type:Boolean,
      default:false
    }
  },
  methods: {
    toggle() {
     this.$emit("input",!this.value);
     this.$emit('change');
    }
  }
};
</script>
<style>
.vue-js-switch.disabled {
  opacity: 1 !important;
}
</style>
<style lang="scss" scoped>
//@import "@/scss/_variables";
.title {
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.3px;
}
.desc {
  margin-top: 10px;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.05rem;
}
</style>
