<template>
  <div class="d-flex flex-column vh-100">
    <div class="page py-4" style="overflow-x:hidden;">
      <div class="iphoneframe-container" ref="iphone">
        <router-link to="/account">
          <BackButton />
        </router-link>
        <transition name="fade-top">
          <PreviewCardWithIphoneFrame
            class="w-100"
            :image="previewCardImage"
            :title="previewCardTitle"
            :desc="previewCardDesc"
            :titleMyan="previewCardTitleMyan"
            :descMyan="previewCardDescMyan"
            :isFindOther="true"
            :isContactUs="false"
            type="shop"
            firstButtonText="Call us now"
            secondButtonText="Check our Facebook"
            v-show="showPreview"
          />
        </transition>
        <div class="mt-4 w-100 d-flex justify-content-center" v-if="showPreview">
          <button
            class="btn btn-circle"
            style="width: 40px; height: 40px;background:#B7B7B7;"
            @click="showPreview = false"
          >
            <CloseIcon class="close-icon" />
          </button>
        </div>

        <div class="mt-4 w-100 title">
          <StageTitle stageNum="1" :title="stageInfoText.title" :desc="stageInfoText.desc" />
        </div>
        <div class="mt-4 p-3 w-100">
          <AddedForm
            v-model="contactInfo"
            @add="addContactInfo"
            :info="info"
            :uploading="photoUploading"
            :type="'shop'"
            @uploadPhoto="uploadPhoto"
          />
        </div>
        <div class="w-100 d-flex justify-content-center">
          <div class="d-flex flex-center btn btn-primary" @click="Preview()">
            <EyeIcon class="eye-icon" />
            <span class="ml-1">Preview</span>
          </div>
        </div>
        <div class="mt-4 w-100 py-4 px-5 bg-gray">
          <MobileToggle
            :title="toggles.contactUsCard.title"
            :desc="toggles.contactUsCard.desc"
            v-model="isEnabledContact"
            @change="addContactInfo"
          />
          <MobileToggle
            class="mt-4"
            :title="toggles.contactUsButton.title"
            :desc="toggles.contactUsButton.desc"
            v-model="isEnabledContactInProductCard"
            @change="addContactInfo"
          />
          <MobileToggle
            class="mt-4"
            :title="toggles.checkFacebook.title"
            :desc="toggles.checkFacebook.desc"
            v-model="isEnabledCheckFBInProductCard"
            @change="addContactInfo"
          />
          <MobileToggle
            class="mt-4"
            :title="toggles.feature.title"
            :desc="toggles.feature.desc"
            v-model="isEnabledShopFeature"
            @change="addContactInfo"
          />
        </div>
        <div class="mt-3 w-100 d-flex flex-center">
          <router-link to="/account/manageproduct">
            <ForwardButton @click.native="$emit('showPage', 1)" />
          </router-link>
        </div>
      </div>
    </div>
    <bottom-navigation-bar />
    <Loading v-if="showLoading" />
  </div>
</template>

<script>
import PreviewCardWithIphoneFrame from "@/components/PreviewCardWithIphoneFrame.vue";
import BackButton from "@/components/BackButton";
import CloseIcon from "@/assets/icons/Close";
import EyeIcon from "@/assets/icons/Eye";
import StageTitle from "@/components/StageTitle";
import MobileToggle from "@/components/MobileToggle";
import AddedForm from "@/components/AddedForm";
import axios from "axios";
import BottomNavigationBar from "@/components/BottomNavigationBar";
import ForwardButton from "@/components/ForwardButton";
import Loading from "@/components/Loading";
import { debounce } from "lodash";

export default {
  components: {
    PreviewCardWithIphoneFrame,
    Loading,
    StageTitle,
    MobileToggle,
    BackButton,
    AddedForm,
    ForwardButton,
    CloseIcon,
    EyeIcon,
    BottomNavigationBar
  },
  data() {
    return {
      showPreview: false,
      showLoading: true,
      title: "",
      toggle: false,
      isEnabledContact: false,
      isEnabledShopFeature: false,
      isEnabledContactInProductCard: false,
      isEnabledCheckFBInProductCard: false,
      previewCardDefaults: {
        title: "Company Name",
        desc: "Company address will be provided here",
        image:
          "https://portalvhdslvb28rs1c3tmc.blob.core.windows.net/yammo/allstaricon/picture.png"
      },
      stageInfoText: {
        title: "Provide your contact Information",
        desc: "Fill your contact information once"
      },
      toggles: {
        contactUsCard: {
          title: "Feature 'Contact Us' Information",
          desc:
            "Enable this option will feature 'Contact us' information as a separate card in product list"
        },
        contactUsButton: {
          title: "Show 'Contact us' button inside product card",
          desc:
            "Enable this option will feature 'Contact us' information as a separate card in product list"
        },
        checkFacebook: {
          title: "Show 'Check our facebook' button inside product card",
          desc:
            "Enable this option will show 'Check our facebook' button in product card"
        },
        feature: {
          title: "Feature my company",
          desc:
            "Enable this option will show 'Check our facebook' button inproduct card"
        }
      },
      contactInfo: {
        title: "",
        desc: "",
        image: "",
        titleMyan: "",
        descMyan: ""
      },
      info: {
        title: "Type company name (Eng)",
        desc: "Type company address here (Eng)",
        titleMyan: "Type company name (Myan)",
        descMyan: "Type company address here (Myan)",
        photoText: "add logo",
        type: "ContactInfo",
        buttons: [
          {
            value: "",
            title: "Type 'contact number'"
          },
          {
            value: "",
            title: "Type 'facebook page url'"
          }
          // {
          //   value: "",
          //   title: "Type more information"
          // }
        ]
      },
      photoUploading: false
    };
  },
  methods: {
    addContactInfo: debounce(async function() {
      let validate = this.validateForm();
      if (!validate) return;
      let obj = {
        Guid: this.shopID,
        Name: this.contactInfo.title,
        Address: this.contactInfo.desc,
        FBPageUrl: this.info.buttons[1].value,
        ContactPhone: this.info.buttons[0].value,
        CustomerID: this.customerID,
        IsEnabledContact: this.isEnabledContact,
        IsEnabledContactInitemCard: this.isEnabledContactInProductCard,
        IsFeatured: this.isEnabledShopFeature,
        IsEnabledCheckFBInitemCard: this.isEnabledCheckFBInProductCard
      };
      if (this.shopID == "" || this.shopID=="0") {
        await axios.post("shop/upsert", obj).then(res => {
          this.$store.commit("updateShopID", res.data.guid);
          let {id,name}=res.data;
          this.$store.commit("updateShop",{id,name});
        });
      } else {
        await axios.put("shop/upsert", obj).then(res=>{
          let {id,name}=res.data;
          this.$store.commit("updateShop",{id,name});
        });
      }
    }, 3000),

    async uploadPhoto(url) {
      let obj = {
        Guid: this.shopID,
        CustomerID: this.customerID,
        Photo: url
      };
      this.photoUploading = true;
      if (this.shopID == "" || this.shopID=="0") {
        await axios.post("shop/uploadphoto", obj).then(res => {
          this.photoUploading = false;
          this.$store.commit("updateShopID", res.data.guid);
          let {id,name}=res.data;
          this.$store.commit("updateShop",{id,name});
        });
      } else {
        await axios.put("shop/uploadphoto", obj).then((res) => {
          this.photoUploading = false;
          let {id,name}=res.data;
          this.$store.commit("updateShop",{id,name});
        });
      }
    },
    async getShop() {
      await axios.get(`shop?customerID=${this.customerID}`).then(res => {
        if (res.data != null) {
          // console.log(res.data);
          this.contactInfo.title = res.data.Name;
          // this.shopID = res.data.Guid;
          this.$store.commit("updateShopID", res.data.Guid);
          this.contactInfo.image = res.data.Photo;
          this.contactInfo.desc = res.data.Address;
          this.info.buttons[0].value = res.data.Phone;
          this.info.buttons[1].value = res.data.FbPageUrl;
          this.isEnabledContact = res.data.IsEnabledContact;
          this.isEnabledShopFeature = res.data.IsEnabledShopFeature;
          this.isEnabledContactInProductCard =
            res.data.IsEnabledContactInProductCard;
          this.isEnabledCheckFBInProductCard =
            res.data.IsEnabledCheckFBInProductCard;
        }
        this.showLoading = false;
      });
    },
    validateForm() {
      if (
        this.contactInfo.title == "" &&
        this.contactInfo.desc == "" &&
        this.info.buttons[1].value == "" &&
        this.info.buttons[2].value == "" 
      ) {
        return false;
      }
      return true;
    },
    Preview() {
      this.$refs.iphone.scrollIntoView();
      this.showPreview = true;
    }
  },
  computed: {
    previewCardTitle() {
      return this.contactInfo.title
        ? this.contactInfo.title
        : this.previewCardDefaults.title;
    },
    previewCardDesc() {
      return this.contactInfo.desc
        ? this.contactInfo.desc
        : this.previewCardDefaults.desc;
    },
    previewCardTitleMyan() {
      return this.contactInfo.titleMyan
        ? this.contactInfo.titleMyan
        : this.previewCardDefaults.title;
    },
    previewCardDescMyan() {
      return this.contactInfo.descMyan
        ? this.contactInfo.descMyan
        : this.previewCardDefaults.desc;
    },
    previewCardImage() {
      return this.contactInfo.image
        ? this.contactInfo.image
        : this.previewCardDefaults.image;
    },
    shopID() {
      return this.$store.state.shopID;
    },
    customerID() {
      return this.$store.state.customerID;
    }
  },
  async mounted() {
    await this.getShop();
  }
};
</script>

<style lang="scss">
@import "@/scss/_PageSetup.scss";
@import "@/scss/_Utilities.scss";
</style>
<style lang="scss">
.page {
  flex: 1;
  overflow-y: auto;
}
.iphoneframe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -1.5rem;
}
</style>
<style lang="scss" scoped>
@import "@/scss/_variables.scss";
.title {
  padding-left: 0.8rem;
}
.btn-primary {
  border-radius: 40px;
  background-color: $lightBlue;
  border-color: unset;
  &:hover {
    border-color: unset;
    box-shadow: none;
  }
  &:active {
    border-color: unset;
    box-shadow: none;
  }
  &:focus {
    border-color: unset;
    box-shadow: none;
  }
}
.eye-icon {
  width: 0.9rem;
  height: auto;
  margin-top: -2px;
}
.btn-circle {
  width: 40px;
  height: 40px;
  background: $grayActive;
}
.close-icon {
  width: 0.8rem;
}
</style>
